.App {
  position: absolute;
  width: 100%;
  height: 100vh;
}

.App:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
}
.container {
  z-index: 1;
  position: absolute;
  width: 100%;
  height: 100vh;
}
@keyframes next_odd {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes next_even {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.sky-gradient-0:after,
.sky-gradient-2:after,
.sky-gradient-4:after,
.sky-gradient-6:after,
.sky-gradient-8:after,
.sky-gradient-10:after,
.sky-gradient-12:after,
.sky-gradient-14:after,
.sky-gradient-16:after,
.sky-gradient-18:after,
.sky-gradient-20:after,
.sky-gradient-22:after,
.sky-gradient-24:after {
  animation: next_even 300s;
}

.sky-gradient-1:after,
.sky-gradient-3:after,
